</template>

<script>
  import Header from '@/components/common/header'
  import Footer from '@/components/common/footer'
export default {
  name: 'App',
  data () {
    return {

    }
  },
  components: {
    Footer, Header
  }
}
</script>

<style>
  @import "../static/css/clear.css";
  @import "../static/css/vue-common.css";

  #app{ height: 100%; }
  .wrap{
    height: auto;
    min-height: 100%;
    margin: 0 auto -90px;
    padding: 0 0 90px;
  }
</style>



